table {
  width: 100%;
}

table td {
  border-collapse: collapse;
  padding: 20px 20px;
  font-size: 13px;
  text-align: left;
  color: #ddd;
  text-align: center;
}

table th {
  border-collapse: collapse;
  padding: 15px 20px;
  font-size: 13px;
  text-align: left;
  text-align: center;
}

.social-url {
  color: var(--button-text-color);
  text-decoration: none;
}

thead th {
  position: sticky;
  top: 0;
  left: 0;
  background-color: #ffc107;
  cursor: pointer;
  text-transform: capitalize;
}

tbody tr:nth-child(even) {
  background-color: #0000000b;
}

tbody tr {
  --delay: 0.1s;
  transition: 0.5s ease-in-out var(--delay), background-color 0s;
}

tbody tr.hide {
  opacity: 0;
  transform: translateX(100%);
}

tbody tr:hover {
  background-color: #333 !important;
}

tbody tr td,
tbody tr td p,
tbody tr td img {
  transition: 0.2s ease-in-out;
}

tbody tr.hide td,
tbody tr.hide td p {
  padding: 0;
  font: 0 / 0 sans-serif;
  transition: 0.2s ease-in-out 0.5s;
}

