
    * {
        padding: 0;
        margin: 0;
        box-sizing: border-box;
        font-family: "Poppins", serif;
        scrollbar-width: thin;
        scrollbar-color: rgba(255, 255, 255, 0.1) #0e0e0e;
    }

    html {
        scroll-behavior: smooth;
    }

    :root {
        --btn-color: linear-gradient(to right, #ffc107, #ffc107);
        --main-color: #22A0EF;
        --main-color: #ffc107;
        --text-color: #BAC5C5;
        --semi-textcolor: #EEEEEE;
        --input-color: #0E131A;

    }



    /* ===== BODY STYLING ==== */
    .maincontainer {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .firstcontainer {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 50px;
    }

    .image {
        position: relative;
    }

    .image img {
        width: 500px;
        height: 500px;
        border-radius: 50%;
    }

    .image .toptext {
        position: absolute;
        top: 10px;
    }

    .image .toptext {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 10px;
        color: var(--text-color);
    }

    .toptext .logotext {
        display: flex;
        align-items: center;
        gap: 2px;
        background-color: rgb(59, 59, 59);
        border-radius: 10px;
        padding: 3px;
    }

    .toptext .logotext i {
        font-size: 20px;
    }


    .secondplace {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        /* background-color: #111; */
        background-color: #111;
        background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9)), url(../pages//assets//banner-candle\ \(1\).png);
        background-size: auto;
        background-position: center;
        background-repeat: no-repeat;
    }

    .formsection {
        background-color: #111;
        background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9)), url(../pages//assets//banner-candle\ \(1\).png);
        background-size: auto;
        background-position: center;
        background-repeat: no-repeat;
    }

    .secondplace .header {
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--text-color);
    }

    form input {
        display: flex;
        flex-direction: column;
        background-color: transparent;
        border: none;
        outline: none;
        color:#f0f0f0;
        border: 0.3px solid rgb(73, 72, 72);
        transition: .3s ease-in-out;
    }

    form .header h3 {
        font-size: 30px;
        margin: 0px 0px 10px 0px;
    }

    form input:focus {
        color: #f0f0f0;
        border: 1px solid var(--main-color);
        box-shadow: 0px 0px 10px var(--main-color);
    }

    form .firstTwoInputs {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        margin: 20px 0px;
    }

    form .firstTwoInputs .firstInput input {
        width: 550px;
        padding: 15px 0px 15px 10px;
        border: 0.3px solid rgb(73, 72, 72);
        border-radius: 6px;
        transition: .3s ease-in-out;
    }

    form .firstTwoInputs .firstInput input:focus {
        border: 1px solid var(--main-color);
        border-radius: 6px;
        box-shadow: 0px 0px 10px var(--main-color);
    }

    form .otherInputs input {
        width: 100%;
        margin: 0px 0px 20px 0px;
        height: 40px;
        border-radius: 3px;
        border: none;
        border: 0.3px solid rgb(73, 72, 72);
        border-radius: 6px;
        padding: 25px 0px 25px 10px;
    }

    form .otherInputs textarea {
        width: 100%;
        height: 150px;
        border-radius: 3px;
        border: none;
        border: 0.3px solid rgb(73, 72, 72);
        border-radius: 6px;
        padding: 25px 0px 25px 10px;
        background-color: transparent;
        outline: none;
        color: #f0f0f0;
        transition: .3s ease-in-out;
    }

    form .otherInputs textarea:focus {
        color: #f0f0f0;
        border: 1px solid var(--main-color);
        border-radius: 6px;
        box-shadow: 0px 0px 10px var(--main-color);
    }

    form .otherInputs input:focus {
        color: #f0f0f0;
        border: 1px solid var(--main-color);
        border-radius: 6px;
    }

    form .check {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    form .btn {
        margin: 20px 0px;
    }

    form .btn button {
        width: 100%;
        padding: 15px;
        border-radius: 6px;
        background-color: var(--main-color);
        border: none;
        color: #111;
        cursor: pointer;
    }

    form .apps {
        /* display: flex;
        align-items: center;
        justify-content: center;
        text-align: right;
        gap: 20px;
        margin: 20px 0px;
        width: 100%;
        cursor: pointer; */

    }

    .apps {
        display: flex;
        gap: 30px;
    }

    .iframe {
        width: 100%;
        padding-top: 50px;
    }

    form .apps .google,
    .apple {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid var(--main-color);
        width: 190px;
        border-radius: 4px;
        padding: 5px 50px;
        text-align: center;
    }

    form .apps i {
        font-size: 20px;
    }

    form .apps .google {
        display: flex;
        align-items: center;
    }

    form .apps .apple {
        display: flex;
        align-items: center;
    }

    form .footer {
        display: flex;
        align-items: center;
        text-align: center;
        width: 100%;
        gap: 5px;
    }


    input::placeholder, textarea::placeholder {
        font-size: 15px;
        color: #f0f0f0;
    }


    .reachout {
        text-align: center;
        background-color: transparent;
        padding: 40px;
        color: var(--text-color);
        border: 0.3px solid rgb(73, 72, 72);
        border-radius: 6px;
        margin: 40px 0px 0px 0px;
        width: 300px;
        height: 300px;
    }

    .reachout-icon .bx {
        padding: 30px;
        color: #0E0E0E;
        border-radius: 50%;
        font-size: 50px;
        background-color: var(--main-color);
        margin: 0px 0px 20px 0px;
    }

    .iframe iframe {
        width: 100%;
        height: 500px;
    }

    footer a {
        text-decoration: none;
        color: var(--text-color);
    }

    .footercontact {
        color: #EEEEEE;
        display: flex;
        align-items: center;
        justify-content: space-around;
        background-color: rgb(31, 30, 30);
        padding: 25px 0px;
    }

    .footercontact .contacts {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    .generalcontact {
        display: flex;
        align-items: center;
        gap: 100px;
    }

    .footeremail {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    .footerphone {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    .contacts a {
        text-decoration: none;
    }

    .contacticon {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .footercontact .contacticon .icon {
        border: 1px solid rgb(2, 32, 51);
        padding: 6px 8px;
    }

    .footerlink {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 120px;
    }

    .footerlink .navigators a {
        text-decoration: none;
        color: var(--semi-textcolor);

    }

    .copyright p {
        color: var(--text-color);
    }





    /* =====  RESPONSIVE DESIGN ======= */

    @media(max-width: 1200px) {

        /* body {
            padding: 20px;
        } */

        .first-nav {
            display: none;
        }

        .linkcontainer {
            display: block;
        }

        .linkcontainer {
            display: none;
        }

        .second-nav {
            display: flex;
            justify-content: space-between;

        }

        .second-nav {
            border-top: none;
            border-bottom: none;
        }

        .generalmenu {
            display: flex;
            padding: 0px 10px;
        }

        .generalmenu i {
            font-size: 25px;
        }

        .cancelbar {
            display: none;
        }

        .imagetext {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: var(--text-color);
            padding: 0px 50px;
        }

        form .firstTwoInputs .firstInput input {
            width: 100%;
            padding: 15px 0px 15px 10px;
            border: 0.3px solid rgb(73, 72, 72);
            border-radius: 6px;
        }

        form .firstTwoInputs {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 100%;
            gap: 20px;
            margin: 20px 0px;
        }

        form .apps {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: right;
            gap: 20px;
            margin: 20px 0px;
            width: 100%;
            cursor: pointer;

        }

        .iframe iframe {
            width: 100%;
            height: 200px;
        }

        .generalcontact {
            display: flex;
            flex-direction: column;
        }

        .footercontact .contacticon .icon {
            border: 2px solid rgb(2, 32, 51);
            padding: 6px 8px;
            background-color: #060606;
            border-radius: 5px;
        }

        .footercontact .contacticon .icon a {
            color: var(--text-color);
        }

        .footercontact {
            display: flex;
            flex-direction: column;
        }

        .footerlink {
            display: none;
        }

        .generalcontact .contacts {
            display: flex;
            flex-direction: column;
        }

        .footerlogo {
            margin: -100px 0px 0px 0px;
        }

        .footer {
            padding: 50px 0px;
            background-color: #060606;
            height: auto;
        }



    }

    @media (max-width: 890px ) {
        .apps {
            display: grid;
            grid-template-columns: repeat(2, 2fr);
        }

        form .otherInputs textarea {
            width: 400px;
        } 
    }

    @media (max-width: 600px) {
        .apps {
            display: grid;
            grid-template-columns: repeat(1, 3fr);
        }
    }

    @media (max-width: 520px) {
        form .otherInputs textarea {
            width: 320px;
        }

     
    }

    
