.list-style3 {
  list-style: none;
  margin: 0;
  padding: 0;
}
.list-style3 li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;
  margin-bottom: 20px;
  position: relative;
  border-bottom: 1px solid #ededed;
}
.list-style3 li:last-child {
  margin-bottom: 0;
}
.list-style3 li:after {
  border-bottom: 2px solid #f9b707;
  content: "";
  height: 1px;
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 0;
  transition: all 0.6s linear 0s;
}
