  margin-bottom: 0;
}
.list-style4 li.active:after {
  width: 100%;
  background: #f9b707;
  border-radius: 10px;
}
.list-style4 li::after {
  position: absolute;
  content: " ";
  display: inline-block;
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  transition: width 0.3s linear;
}
