  font-family: Font Awesome\5 Free;
  position: absolute;
  right: 20px;
  transition: width 0.5s linear;
  z-index: 1;
  font-size: 16px;
  font-weight: 600;
}
.list-style4 li:hover a {
  color: #ffffff;
}
.list-style4 li:hover:after {
  width: 100%;
  background: #f9b707;
  border-radius: 10px;
}
