  top: -46%;
  left: 45%;
  position: absolute;
  animation: animationFramesOne 30s infinite linear;
  position: relative;
  background-color: #f9b707;
  text-align: left;
  -webkit-transform: rotate(-60deg) skewX(-30deg) scale(1, 0.866);
  transform: rotate(-60deg) skewX(-30deg) scale(1, 0.866);
  width: 15px;
