.video_btn {
  position: relative;
  height: 80px;
  width: 80px;
  background: #f9b707;
  text-align: center;
  display: inline-block;
  line-height: 85px;
  color: #fff;
  border-radius: 50%;
  transition-duration: 0s;
}
.video_btn:hover i,
.video_btn:focus i {
  color: #fff;
}
.video_btn:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  height: 80px;
  width: 80px;
  border: 1px solid #fff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transform: translateX(-50%) translateY(-50%);
  opacity: 0.3;
  animation: pulse-border 1500ms ease-out infinite;
}
.video_btn:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  height: 95px;
  width: 95px;
  border: 1px solid #fff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transform: translateX(-50%) translateY(-50%);
  opacity: 0.3;
  animation: pulse-border 1500ms ease-out infinite;
}
.video_btn.small {
  width: 50px;
  height: 50px;
  line-height: 50px;
}
.video_btn.small:after {
  height: 50px;
  width: 50px;
}
.video_btn.small:before {
  height: 65px;
  width: 65px;
}
@keyframes pulse-border {
  0% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
  }
}
@media screen and (max-width: 1199px) {
  .video_btn {
    height: 75px;
    width: 75px;
    line-height: 80px;
  }
  .video_btn:after {
