.roadmap-content {
  text-align: center;
  max-width: 200px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 38px;
  position: relative;
}
.roadmap-content .roadmap-year {
  background: #282b2d;
  -webkit-border-radius: 5px 5px 0 0;
  -moz-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0;
  padding: 9px 5px;
  position: relative;
}
.roadmap-content .roadmap-year .road-map-date {
  font-weight: 600;
  font-size: 20px;
  margin: 0;
  color: #fff;
}
.roadmap-content .road-map-title {
  padding: 0px 12px;
  background: rgba(249, 183, 7, 0.7);
  color: #282b2d;
  -webkit-border-radius: 0 0 5px 5px;
  -moz-border-radius: 0 0 5px 5px;
  border-radius: 0 0 5px 5px;
  position: relative;
  display: table;
  height: 75px;
  width: 100%;
}
.roadmap-content.even .roadmap-year:before {
  position: absolute;
  content: "";
  top: -27%;
  left: 50%;
  -webkit-transform: translate(-50%);
  -ms-transform: translate(-50%);
  transform: translate(-50%);
  width: 16px;
  height: 12px;
  clip-path: polygon(0 100%, 50% 0, 100% 100%);
  background: #282b2d;
}
.roadmap-content.odd .road-map-title:before {
  position: absolute;
  content: "";
  bottom: -15%;
  left: 50%;
  -webkit-transform: translate(-50%);
  -ms-transform: translate(-50%);
  transform: translate(-50%);
  width: 16px;
  height: 12px;
  clip-path: polygon(0 0, 50% 100%, 100% 0);
  background: rgba(249, 183, 7, 0.7);
}
.roadmap-content .road-map-title span {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  overflow: hidden;
  display: table-cell;
  vertical-align: middle;
}
.roadmap-content.even:before,
.roadmap-content:after,
.roadmap-content.odd:before,
.roadmap-content:after {
  content: "";
  position: absolute;
  /* background: #007aff; */
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  position: absolute;
}
.roadmap-content.even:before {
  height: 12px;
  width: 12px;
  background: rgba(249, 183, 7, 0.9);
  top: 4px;
}
.roadmap-content.odd:before {
  height: 12px;
  width: 12px;
  background: rgba(249, 183, 7, 0.9);
  bottom: -11px;
}
.roadmap-content.odd:after {
  background: rgba(249, 183, 7, 0.5);
  width: 18px;
  height: 18px;
  bottom: -17px;
}
.roadmap-content.even:after {
  background: rgba(249, 183, 7, 0.5);
  width: 18px;
  height: 18px;
  top: 4px;
}
.roadmap-carousel:before {
  top: 50%;
