@-webkit-keyframes topRight {
  0% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
  50% {
    -webkit-transform: translate(80px, -80px);
    transform: translate(80px, -80px);
  }
  100% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}
@keyframes topRight {
  0% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
  50% {
    -webkit-transform: translate(80px, -80px);
    transform: translate(80px, -80px);
  }
  100% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}
