/* Navbar Wrapper */
.unique-navbar {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #000;
    color: white;
    padding: 20px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
  }
  
  /* Logo and Branding */
  .unique-navbar-brand {
    display: flex;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
    color: gold;
    text-decoration: none;
  }
  
  .unique-navbar-logo {
    /* height: 100px; */
    width: 100px;
    /* margin-right: 10px; */
  }
  
  /* Navigation Links */
  .unique-navbar-links {
    display: flex;
    gap: 40px;
    position: relative;
    top: 20px;
    /* margin-top: 30px; */
  }
  
  .unique-navbar-links a {
    color: white;
    text-decoration: none;
    /* font-size: 16px; */
    transition: 0.3s ease-in-out;
    font-size: 14px;

  }
  
  .unique-navbar-links a:hover {
    color: gold;
  }
  
  /* Authentication Buttons */
  .unique-auth-buttons {
    display: flex;
    gap: 10px;
  }


  /* .unique-auth-buttons button a {
    color: #fff;
    text-decoration: none;
  } */
  
  .unique-auth-buttons button {
    padding: 8px 15px;
    border: none;
    cursor: pointer;
    font-weight: bold;
    transition: 0.3s ease-in-out;
  }
  
  .unique-login-btn a {
   color: #fff;
    text-decoration: none;
  }

  .unique-login-btn {
    background-color: transparent;
    /* color: white; */
    border: 1px solid #ffc107;
    border-radius: 5px;
  }
  
  .unique-login-btn:hover {
    background-color: none;
    outline: 1px solid #ffc107;
    border-radius: 5px;
    color: #000;
  }
  
  .unique-signup-btn a {
    text-decoration: none;
    color: #000;
  }

  .unique-signup-btn {
    background-color: #ffc107;
    color: black;
    border-radius: 5px;
    border: 1px solid transparent;
  }
  
  .unique-signup-btn:hover {
    /* border: 1px solid #ffc107; */
    color: #fff;
    background: none;
  } 
  
  /* Mobile Menu (Hidden by default) */
  .unique-menu-toggle {
    display: none;
    cursor: pointer;
    font-size: 20px;
    color: white;
  }

  @media (min-width: 1100px) {
    .unique-auth-button {
        display: none !important;
    }
  }
  
  /* Responsive Design */
  @media (max-width: 1100px) {
    .unique-navbar-links {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 80px;
      right: 0;
      background: black;
      padding: 10px;
      width: 100%;
      border-radius: 5px;
    }

    .unique-navbar-links a {
      font-size: 18px;
      font-size: 600;
    }

    .unique-auth-buttons {
      display: none !important;
  }
  
    .unique-navbar-links.active {
      display: flex;
    }
  
    .unique-menu-toggle {
      display: block;
    }
  
    .unique-auth-button {
      /* flex-direction: column; */
      display: flex;
      gap: 10px;
      margin-top: 10px;
    }

    .unique-auth-button button {
      padding: 5px 20px;
    }
  }


@media (max-width: 700px) {
  .unique-navbar-logo {
    height: 40px;
    width: 70px;
  }
}