.stats-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
  /* padding: 20px 0; */
  position: relative;

}

.triangle-left {
  width: 0;
  height: 0;
  border-top: 80px solid transparent;
  border-bottom: 80px solid transparent;
  /* border-left: 100px solid #ffc107; */
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.triangle-right {
  width: 0;
  height: 0;
  border-top: 80px solid transparent;
  border-bottom: 80px solid transparent;
  /* border-right: 100px solid#ffc107; */
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.stats-content {
  background: black;
  padding: 20px;
  width: 50%;
  border-radius: 10px;
  z-index: 1;
}

.text-gold {
  color: #ffc107;
  font-weight: 700;
  font-size: 25px;
}

.stats-text {
  color: #cccc;
}

.stat-box {
  padding: 20px;
}

.stat-box p {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.stat-box p i {
  font-size: 30px;
  color: #ff9700;
}

.heatmap {
  /* height: 70px; */
}

@media (max-width: 768px) {
  .stats-container {
    flex-direction: column;
    text-align: center;
    padding: 30px 10px;
  }

  .triangle-left, .triangle-right {
    display: none;
  }

  .stats-content {
    width: 100%;
