.card-body{
  position: relative;
  width: 100%;
  background: #000000;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 5px 25px rgb(0 0 0 / 20%);
  /* margin-top: 60px; */
  border-radius: 20px;
}

.card-body .top-shape{
  clip-path: polygon(0 0, 100% 0%, 100% 50%, 50% 75%, 0 50%);
  height: 200px;
}

.card-01 .card-body .top-shape{
  border-radius: 20px;
  /* background: var(--card-01-color-01); */
}

.card-02 .card-body .top-shape{
  background: var(--card-02-color-01);
}

.card-03 .card-body .top-shape{
  background: var(--card-03-color-01);
}

.card-body .top-shape:before{
  content: '';
  position: absolute;
  clip-path: polygon(0 0, 100% 0%, 100% 25%, 50% 50%, 0 25%);
  width: 100%;
  height: 200px;
}

.card-01 .card-body .top-shape:before{
  background: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.6)), url(https://i.pinimg.com/236x/25/e4/35/25e435eb70cf1ac5bf442fadafadc9a9.jpg);
  background: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.6)), url(https://i.pinimg.com/474x/4e/0c/ec/4e0cec0873d60eb35ec576c3081cc4cc.jpg);
  /* background: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.6)), url(https://i.pinimg.com/236x/e7/65/70/e765702c71d834e100158434a95f85b1.jpg); */
 /* background: #0056b3; */                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px 10px 0 0;
}


.top-part {
  position: relative;
