}

/* Slider container */
.slider-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  overflow: hidden;
  position: relative;
}

/* Slider track */
.slider-track {
  display: flex;
  width: 100%;
  transition: transform 0.5s ease-in-out;
}

/* Testimonial cards */
.testimonial-card {
  background: white;
  border: none;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
