/* @import url(../portfolio/RootColors.css); */
.overlay {
    position: fixed; /* Cover the entire screen */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #13171A; /* Slightly dimmed background */
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index:  999999; /* Ensure the overlay is on top */
    pointer-events: none; /* Prevent clicks during loading */
  }


  /* From Uiverse.io by alexruix */ 
.loader-main {
  --cell-size: 30px;
  --cell-spacing: 1px;
  --cells: 3;
  --total-size: calc(var(--cells) * (var(--cell-size) + 2 * var(--cell-spacing)));
  display: flex;
  flex-wrap: wrap;
  width: var(--total-size);
  height: var(--total-size);
  /* left: 50%;
  top: 50%;
  width: 72px;
  height: 72px;
  margin-left: -36px;
  margin-top: -36px; */
}

.cell {
  flex: 0 0 var(--cell-size);
  margin: var(--cell-spacing);
  background-color: transparent;
  box-sizing: border-box;
  border-radius: 50px;
  animation: 1.5s ripple ease infinite;
}

.cell.d-1 {
  animation-delay: 100ms;
}

.cell.d-2 {
  animation-delay: 200ms;
}

.cell.d-3 {
  animation-delay: 300ms;
}

.cell.d-4 {
  animation-delay: 400ms;
}

.cell:nth-child(1) {
  --cell-color:  #ffc107;
  /* --cell-color: #00FF87; */
}

.cell:nth-child(2) {
  --cell-color: #ffc107;
  /* --cell-color: #0CFD95; */
}

.cell:nth-child(3) {
  --cell-color: #ffc107;
}

.cell:nth-child(4) {
  --cell-color: #ffc107;
}

.cell:nth-child(5) {
  --cell-color:#ffc107;
}

.cell:nth-child(6) {
  --cell-color: #ffc107;
  /* --cell-color: #3DF5D4; */
}

.cell:nth-child(7) {
  --cell-color:#ffc107;
}

.cell:nth-child(8) {
  --cell-color: #ffc107;
}

.cell:nth-child(9) {
  --cell-color:#ffc107;
  /* --cell-color: #60EFFF; */
}

/*Animation*/
@keyframes ripple {
  0% {
    background-color: transparent;
  }

  30% {
    background-color: var(--cell-color);
  }

  60% {
    background-color: transparent;
  }

  100% {
    background-color: transparent;
  }
}

