:root {
    --btnColor :#FFD600 ;
    --bgColor: #212529;
    --cardColor: #191C1F;
    --textDeep: #6C757D;
    --textLight: #DEE2E6;
    --textBright: #fff;
  --cell-color:  #ffc107;

}

footer {
    /* background-color: #05283C; */
}

.footer-wrapper-main {
    /* background-color: #000; */
    color: #87CEEB;
    color: #4682B4;
    color: #274472;
    color: #3F51B5;

}

.footer-section {
    padding: 50px 0;
    /* background-color: #000; */
}

.footer-header {
    display: flex;
    /* gap: 600px; */
    justify-content: space-between;
}

.footer-btn-links {
    display: flex;
    gap: 20px;
}

.footer-btn-links a {
    text-decoration: none;
    font-size: 13px;
}

.footer-btn-one {
    border: 1px solid var(--cell-color);
    padding: 5px 10px;
    border-radius: 5px;
    height: 32px;
    width: 100%;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 13px;
}

.footer-btn {
    padding: 5px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    height: 32px;
    background-color: var(--cell-color);
    color: #111;
}

.footer-btn-one:hover {
    background-color: var(--cell-color);
    transition: .5s ease-in-out;
    color: #111;
}
.footer-btn:hover {
    outline: 1px solid var(--cell-color);
    transition: .5s ease-in-out;
    background: none !important;
    color: var(--cell-color);
}
#footerBtn {
    color: var(--cell-color);
    cursor: pointer;
    margin-right: 10px;
}

.footer-group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 100px;
}

.footer-group {
    line-height: 2;
    margin: 30px 0;
    color: #fff;
}

.footer-group span, .footer-group a {
    display: block;
    font-size: 13px;
    text-decoration: none;
    color: #fff;
    font-weight: 300;
    cursor: pointer;
}

.footer-group span:hover, .footer-group a:hover {
    text-decoration: underline;
    transition: .5s ease-in-out;
    color: var(--cell-color);
}
.footer-logo {
    /* display: flex; */
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.footer-logo span:nth-child(even) {
    font-size: 16px;
    color: var(--cell-color);
}

.footer-logo img{
    /* display: flex; */
    font-size: 15px;
    text-decoration: none;
    height: 40px;
    width: 50px;
}

.footer-two-content {
    display: flex;
    gap: 400px;
    text-align: center;
    justify-content: center;
    align-items: center;
    /* background-color: #C7DEE2; */
    border: 1px solid var(--cell-color);
    padding: 15px 0;
    border-radius: 50px;
}

.social-icons i{
    border: 1px solid #05283C;
    padding: 5px;
    border-radius: 20px;
    margin: 0 2px;
    color: var(--cell-color);
}

.social-icons i:hover {
    background-image: var(--bg-color);
    color: #fff;
    transition: .5s ease-in-out;
    transform: translateY(-5px);
}

.footer-logo {
    font-size: 25px;
}

.disclaimer-content p {
    font-size: 12px;
    font-weight: 200;
    color: #fff;
    margin-top: 40px;
}

.general-footer-wrap {
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.9)), url(https://img.freepik.com/premium-photo/zen-wallpaper-with-bitcoin-design-set-against-zen-inspi-illustration-crypto-trading-backgroundr_1020495-317995.jpg?ga=GA1.1.173702916.1721990286&semt=ais_hybrid);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100%;
    width: 100%;
    background-color: #000;
}


@media (max-width: 460px) {
    footer {
        /* height: 100vh; */
        line-height: 2;
    }
    .footer-header {
        gap: 10px;
        justify-content: center;
        align-items: center;
        text-align: center;
        flex-direction: column;
        line-height: 1;
    }

    .footer-group {
        gap: 40px;
        display: grid;
        grid-template-columns: repeat(2, 2fr);
        justify-content: center;
        align-items: center;
    }

    .footer-two-content {
        gap: 20px;
        flex-direction: column;
    }

    .modal-group a {
        font-size: 14px;
    }

    .social-icons i {
        font-size: 30px;
        border: none;
    }
}

@media (max-width: 420px) {
    .footer-header {
        gap: 10px;
    }
}

@media (max-width: 390px) {
    
    footer {
        /* text-align: center; */
    }

    .footer-header {
        gap: 40px;
    }

    .footer-group {
        /* gap: 9px; */
        width: 360px;
        justify-content: center;
        /* text-align: center; */
    }

}

@media (max-width: 375px) {
    .footer-group {
        /* gap: 9px; */
        width: 349px;
        font-size: 10px;
    }

    .footer-header {
        gap: 20px;
    }

    .footer-group  a {
        font-size: 12px;
    }
}
