/* Header */
.myBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    /* backdrop-filter: blur(10px); */
    background: #111;
    color: white;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99999;
  }
  
  /* Logo */
  .myLogo img{
    height: 40px;
    width: 50px;
  }
  
  /* Buttons */
  .myToggleBtn, .myCloseBtn {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 30px;
    /* border: 1px solid #ffc107; */
    border-radius: 3px;
    height: 35px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 10px; */
  }

  .main-li-two li {
    font-size: 14px;
    font-weight: 500;
  }
  
  /* Sidebar */
  .myPanel {
    position: fixed;
    top: 70px;
    left: 0;
    width: 250px;
    height: 100%;
    background: #111;
    /* color: #fff; */
    padding: 20px;
    z-index: 1000;
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Sidebar Menu */
  .myList {
    list-style: none;
    padding: 0;
  }

  .myPanel .main-li {
    position: relative;
    left: -11px;
    font-weight: 600;
    font-size: 14px;
    background: #333;
    border-radius: 10px 0 10px 0;
    color: #fff;
  }


  .main-li-two:focus {
    /* background-color: #1111; */
  }

  .myList li {
    margin: 10px 0;
    list-style-type: none;
    font-weight: 600;
  }
  
  .myList a {
    color: white;
    text-decoration: none;
    display: block;
    padding: 10px;
  }

  
  .myList .main-li a {
    /* color: #111; */
  } 
  
  /* Dropdown */
  .myDropdownBtn {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    display: flex;
    align-items: center;
    width: 100%;
    text-align: left;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1px;
    gap: 3px;
  }
  
  .myDropdown {
    display: none;
    list-style: none;
    padding-left: 15px;
  }
  
  .myDropdown.open {
    display: block;
  }
  
  .dashboard-main-stats {
    background-image: linear-gradient(rgba(0,0,0,0.9), rgba(0,0,0,0.9)), url(https://media.istockphoto.com/id/1003433354/photo/simple-bar-and-line-chart-on-dark-backgorund.webp?a=1&b=1&s=612x612&w=0&k=20&c=hkZnhYM5woZJqzlYLrWoccxERCpLBkNQQEtlRDkzKT0=);
    height: 200px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    padding: 10px;
    position: relative;
    /* border: 1px solid ; */
    border-left: 1px solid #888;
    border-bottom: 1px solid #888;
  }

  .dashboard-main-stats .site-anime {
    height: 60px;
    width: 60px;
    border-radius: 30px;
    border: 2px dashed #ffc107;
    /* border-bottom: 2px solid transparent; */
    position: absolute;
    top: 30px;
    right: -30px;
    animation: rotate 3s linear infinite;
  }

  .stats-card-wrap h6 {
    background: linear-gradient(45deg, #ffc107, #fff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
  }

 
  

   @keyframes rotate {
      from {
        transform: rotate(0deg);
      } 
      to {
        transform: rotate(360deg);
      }
   }

  .dashboard-main-stats .stats-header {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    /* top: 0; */
    position: absolute;
    top: -10px;
    right: -10px;
    border-radius: 10px;
  }

  .stats-header h4 {
    color: #cccc;
    font-weight: 700;  
    position: relative;
    left: -80px;
    top: 20px;
    font-size: 15px;
  }

  .stats-header .back-to-site {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffc107;
    /* background-color: #fff; */
    background: linear-gradient(to left, #888, transparent);
    height: 60px;
    width: 60px;
    padding: 3px 10px;
    border-radius: 35px;
    /* border-right: 2px solid #ffc107;
    border-bottom: 2px solid #ffc107; */
  }

  .stats-header .back-to-site span a {
    text-decoration: none;
    color: #ffc107;
  }

  .dashboard-main-stats .action-btn-wrapper {
    position: absolute;
    bottom: -17px;
    display: flex;
    justify-content: space-between;
    gap: 50px;
  }

  .dashboard-main-stats .action-btn-wrapper a {
    text-decoration: none;
    color: #111;
    background-color: #ffc107;
    border-radius: 10px 0 10px 0;
    padding: 5px 10px;
    box-shadow: 0px 0px 10px #111;
  }

  .dashboard-main-stats .user-balance-wrapper {
    text-align: center;
    line-height: 1;
  }

  .dashboard-main-stats .user-balance-wrapper span {
    font-weight: 800;
    color: #888;
    font-size: 40px;
  }

  .dashboard-main-stats .user-balance-wrapper p {
    color: #ffc107;
  }

  .policy-container {
    background-image: linear-gradient(rgba(0,0,0,0.9), rgba(0,0,0,0.9)), url(https://media.istockphoto.com/id/1296973037/photo/global-business-and-network-connection-concept.jpg?s=612x612&w=0&k=20&c=Xakb8baFtJ9Kp10t7Jq-QFc1lDfIeBsnBFxNFRyTkjw=);
    background-position: center;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    padding: 50px 0;
    background-attachment: fixed;
  }


  @media (min-width: 500px) {
    .dashboard-balance {
      display: none !important;
    }
  }
  @media (max-width: 500px) {
      .dashboard-main-stats-two {
        display: none !important;
      }
  }


  @media(min-width: 1200px) {
    .mobile-nav-wrapper {
        display: none !important;
    }
    
  }
  

  @media(max-width: 1199px) {
    #sidebar {
        display: none !important;
    }
  }